var $ = require('./jQuery.js');
var salesforce = function () { };
salesforce.jsforce = require('./jsforce.js')
window.console.log("mindcontrol - salesforce common");
salesforce.getSalesforceObjectIdFromUrl = function (url) {
	try {
		return url.split("sObject/")[1].split("/")[0];
	} catch (e) {

	}
}
salesforce.getSalesforceObjectType = function (objectId) {
	var prefix = objectId.substring(0, 3);
	switch (prefix) {
		case '001':
			return 'Account';
			break;
		case '006':
			return 'Opportunity';
			break;
		case '00Q':
			return 'Lead';
			break;
		case '003':
			return 'Contact';
			break;
		default:
			return 'Other';
			break;
	}
}

salesforce.hijackLinkClicksAndDrags = function () {

	$("body").get(0).addEventListener('click', function (e) {
		var target = $(e.target);
		if (target.hasClass('forceOutputLookup')) {
			e.preventDefault();
			e.stopPropagation();
			handleSalesforceLinkClick(e.target.href);
			return false;
		}
	}, true);

	function getObjectForSharing(objectId, title) {
		var objectType = salesforce.getSalesforceObjectType(objectId)
		var data = {}
		data['salesforce.' + objectType.toLowerCase()] = {
			objectId: objectId,
			description: 'Salesforce ' + objectType + (title ? ': ' + title : '')
		}
		return data;

	}

	function handleSalesforceLinkClick(link) {
		var objectId = salesforce.getSalesforceObjectIdFromUrl(link);
		var data = getObjectForSharing(objectId);

		FSBL.Clients.DataTransferClient.openSharedData({
			data: data
		});
	}

	$(document).on('dragstart', 'a.forceOutputLookup', function (event) {
		var objectId = salesforce.getSalesforceObjectIdFromUrl(event.currentTarget.href);
		var data = getObjectForSharing(objectId, FSBL.$(event.currentTarget).text());
		FSBL.Clients.DataTransferClient.dragStartWithData(event.originalEvent, data);
	});
}

salesforce.login = function (cb) {

	function signon(signOnData) {
		console.log("signonData", signOnData);
		if (signOnData) {
			$('#username').val(signOnData.username);
			$('#password').val(signOnData.password);
			FSBL.Clients.WindowClient.setComponentState({ field: 'loginRequested', value: { activeLogin: true, validationRequired: signOnData.validationRequired } }, function () {

				$('#login_form').submit();
			});
		} else { // what to do if they close the sign on box?
			FSBL.Clients.WindowClient.close();
		}
	}
	if (location.href.includes('identity/verification')) {
		cb('Two Step Verification')
		return;
	}

	// am I on a login page?
	var signOnKey = 'salesforce';
	if ($('#Login').length) {
		params = {
			icon: 'https://chartiq.my.salesforce.com/img/logo198.png',
			prompt: 'Salesforce Login'
		};
		forceparams = {
			icon: 'https://chartiq.my.salesforce.com/img/logo198.png',
			prompt: 'Salesforce Login',
			force: true
		};

		// bad password
		if ($('#error').text().includes('check your username and password')) {
			FSBL.Clients.WindowClient.getComponentState({ field: 'loginRequested' }, function (err, state) {
				if (state.validationRequired) {
					FSBL.Clients.AuthenticationClient.appRejectAndRetrySignOn(signOnKey, { userMsg: $('#error').text() }, function (err, signOnData) {
						signon(signOnData);
					});
				} else {
					// force a signon problem because previously accepted data wasn't successful
					FSBL.Clients.AuthenticationClient.appSignOn(signOnKey, forceparams, function (err, signOnData) {
						signon(signOnData);
					});
				}
			});
		} else {
			FSBL.Clients.AuthenticationClient.appSignOn(signOnKey, params, function (err, signOnData) {
				signon(signOnData);
			});
		}
		// Hide login box
		$(document.body).append('<style>#content {display: none! important}</style>');
		return;
	} else {
		FSBL.Clients.WindowClient.getComponentState({ field: 'loginRequested' }, function (err, state) {
			if (state && state.activeLogin) {
				if (state.validationRequired) {
					FSBL.Clients.AuthenticationClient.appAcceptSignOn(signOnKey);
					cb(null);
					return;
				}
				FSBL.Clients.WindowClient.setComponentState(
					{
						field: 'loginRequested', value: { activeLogin: false, validationRequired: false }
					},
					function () {
						cb(null);
					});
			} else { //active session already
				cb(null);
			}
		});
	}
}

salesforce.hideHeader = function () {
	var header = document.getElementsByTagName('header');
	if (header.length > 1) {
		// hide header
		header[0].style.display = "none";
		header[1].style.display = "none";
		var section = document.getElementsByTagName('section');
		section[0].style.top = "0px";
		$('.stage.panelSlide').css('height', '100%');

	} else {
		setTimeout(salesforce.hideHeader, 250);
	}
}

salesforce.hideSidebar = function () {
	$(document.body).append(`<style>
		.column.region-sidebar-right {
			display:none !important;
		}
		.column.region-main {
			max-width: 100% !important;
		}

	</style>`)
}

salesforce.apiLogin = function (params, cb) {
	if (!params.baseUrl) params.baseUrl = location.protocol + '//' + location.hostname;
	if (!params.redirectUri) params.redirectUri = params.baseUrl + '/one/one.app'
	if (!params.clientId) params.clientId = '3MVG9zlTNB8o8BA1I4NIH8YcOPd8yWUcXA6f.a1NenSjwKI24kLEMFuk8oTKPPjYJq7PApCw2c0O3X3zIeiza' //dev salesforce
	// chartiq salesforce: '3MVG9Km_cBLhsuPweiuLObSgy5jSO4wgz8dp5VZhHwHwTPDFdIs848H8WpGlYRa.sVxKFOP6qbloFskRBeqrZ'

	// remove trailing / from baseUrl and replace lightning.force with salesforce to prevent cross-domain issues
	params.baseUrl = params.baseUrl.replace(/\/$/, "");
	params.baseUrl = params.baseUrl.replace("lightning.force", "salesforce");

	if (!salesforce.listener) {
		FSBL.Clients.RouterClient.addListener('Finsemble.SalesforceApiLogin', function (err, response) {
			window.jsforcelocation = response.data;
			if (salesforce.jsforce.browser.init2({
				clientId: params.clientId,
				redirectUri: params.redirectUri,
				instanceUrl: params.baseUrl
			})) {
				debugger;
				cb(null, salesforce.jsforce.browser.connection);
			}
		});
		salesforce.listener = true;
	}

	salesforce.jsforce.browser.init({
		clientId: params.clientId,
		redirectUri: params.redirectUri,
		instanceUrl: params.baseUrl
	})

	salesforce.jsforce.browser.on('connect', function (conn) {
		cb(null, conn);
	})

	if (!salesforce.jsforce.browser.isLoggedIn()) {
		salesforce.jsforce.browser.login();
	} else {
		cb(null, salesforce.jsforce.browser.connection);
	}
}

salesforce._createTask = function (params, cb) {
	debugger;
	let objectType = salesforce.getSalesforceObjectType(params.objectId);
	var task = {
		Subject: params.subject,
		Status: (params.status ? params.status : 'Completed'),
		ActivityDate: (params.date ? params.date : new Date())
	}
	switch (objectType) {
		case 'Contact':
		case 'Lead':
			task.WhoId = params.objectId;
			break;
		default:
			task.WhatId = params.objectId;
			break;
	}

	params.conn.sobject('Task').create(task, function (err, ret) {
		if (err || !ret.success) {
			cb(err, null);
			return;
		}
		cb(null, ret);
	})

}

salesforce.createTask = function (params, cb) {
	// login
	if (!params.accessDeclined) {
		params.accessDeclined = 0;
	}
	if (params.accessDeclined > 1) {
		return;
	}
	salesforce.apiLogin(params, function (err, conn) {
		debugger;
		if (params.alreadyProcessed) return; // this somehow gets called multiple times, prevent creation of multiple tasks
		params.alreadyProcessed = true;
		params.conn = conn;
		if (params.searchTerm && params.objectType) {
			conn.search(`FIND {${params.searchTerm}} IN ALL FIELDS RETURNING ${params.objectType}(Id)`, function (err, ret) {
				if (err) {
					params.accessDeclined++;
					params.alreadyProcessed = false;
					salesforce.createTask(params, cb);
					return;
				}
				if (ret.searchRecords.length == 1) {
					params.objectId = ret.searchRecords[0].Id;
					salesforce._createTask(params, cb);
				} else if (ret.searchRecords.length > 1) {
					cb("Multiple Records Found in Salesforce.", null);
				} else {
					cb("No Records Found in Salesforce for this " + params.objectType, null);
				}
			})
		} else if (params.objectId) {
			salesforce._createTask(params, cb);
		} else {
			cb("Invalid Search Parameters", null);
		}
	})
}

module.exports = salesforce;


