FSBL.$ = require('./jQuery.js');
FSBL.alertify = require('./alertify.js');

var old_;
if (window._) old_ = window._;
var sf = require('./salesforcecommon.js');
if (old_) window._ = old_;
var console = new FSBL.Utils.Console('Symphony');

//FSBL.$ = jQuery;
console.log("mindcontrol");
FSBL.addEventListener('onReady', function () {
	var signOnKey = 'symphony';
	FSBL.$(document.body).append(`<style>
		.simple_grid_main_container {
			top: 0px !important;
			left: 0px !important;
		}
		[data-tooltip]:hover:before, [data-tooltip]:hover:after, [data-tooltip]:focus:before, [data-tooltip]:focus:after, .fsbl-tooltip:hover:before, .fsbl-tooltip:hover:after, .fsbl-tooltip:focus:before, .fsbl-tooltip:focus:after, .fsbl-tooltip-top:hover:before, .fsbl-tooltip-top:hover:after, .fsbl-tooltip-top:focus:before, .fsbl-tooltip-top:focus:after {
			transform:translateY(4px) !important;
		}
		/* symphony defaults to zero, which causes issues with fsbl header */
		#FSBLHeader * {
			min-width: auto;
			min-height: auto;
		}

		/* Override symphony minimum of 620px */
		body {
			min-width: auto;
		}

		</style>`);



	function signon(signOnData) {
		console.log("signonData", signOnData);
		if (signOnData) {

			FSBL.$("input[name='signin-email']").val(signOnData.username);
			FSBL.$("input[name='signin-password']").val(signOnData.password);
			FSBL.Clients.WindowClient.setComponentState({ field: 'loginRequested', value: { activeLogin: true, validationRequired: signOnData.validationRequired } }, function () {
				FSBL.$("button[name='signin-submit']").trigger('click');
				setTimeout(checkIfSignonNeeded, 1000);
			});
		} else { // what to do if they close the sign on box?
			FSBL.Clients.WindowClient.close();
		}
	}

	function amSignedOn() {
		FSBL.Clients.WindowClient.getComponentState({ field: 'loginRequested' }, function (err, state) {
			if (state) {
				FSBL.Clients.AuthenticationClient.appAcceptSignOn(signOnKey);
				FSBL.Clients.WindowClient.setComponentState({ field: 'loginRequested', value: false });
			}
		});
	}

	// am I on a login page?
	function checkIfSignonNeeded() {

		if (FSBL.$('.undefined.tempo-btn.tempo-btn--primary').text() === 'Login') {
			FSBL.$('.undefined.tempo-btn.tempo-btn--primary').trigger('click');
			return true;
		}

		if (FSBL.$('#authentication').length) {
			params = {
				icon: 'https://chartiq.symphony.com/images/svg/logo.svg',
				prompt: 'Symphony Login'
			};
			forceparams = {
				icon: 'https://chartiq.symphony.com/images/svg/logo.svg',
				prompt: 'Symphony Login',
				force: true
			};

			FSBL.Clients.WindowClient.getComponentState({ field: 'loginRequested' }, function (err, state) {
				// bad password
				if (FSBL.$('.fail.show').text().includes('Invalid username or password')) {

					FSBL.Clients.WindowClient.getComponentState({ field: 'loginRequested' }, function (err, state) {
						if (state.validationRequired) {
							console.log("failed login: ", FSBL.$('.fail.show').text());
							FSBL.Clients.AuthenticationClient.appRejectAndRetrySignOn(signOnKey, { userMsg: FSBL.$('.fail.show').text() }, function (err, signOnData) {
								signon(signOnData);
							});
						} else {
							// force a signon problem because previously accepted data wasn't successful
							FSBL.Clients.AuthenticationClient.appSignOn(signOnKey, forceparams, function (err, signOnData) {
								signon(signOnData);
							});
						}
					});
				} else {
					FSBL.Clients.AuthenticationClient.appSignOn(signOnKey, params, function (err, signOnData) {
						signon(signOnData);
					});
				}
			});

			FSBL.$(document.body).append('<style>#authentication {display: none! important}</style>');

			return true;
		}

		return false;
	}

	FSBL.$(document.body).append(`<style>
		#header { display: none !important }

		body > div.tether-element.tether-element-attached-top.tether-element-attached-left.tether-target-attached-bottom.tether-target-attached-center.arrow-top-left.tether-enabled {
			top: 0px;
			left: 0px;
			position: absolute;
			transform: translateX(150px) translateY(55px) translateZ(0px) !important;
		}

		.modal-box {
			width:100% !important;
		}
	</style>`);

	function launchIfNeeded(chatDescriptor) {
		chatDescriptor.chatId = chatDescriptor.userID ? chatDescriptor.userID : chatDescriptor.viewID;
		FSBL.Clients.DataTransferClient.openSharedData({
			data: {
				"symphony.chat": {
					chatDescriptor: chatDescriptor
				}
			}
		})
	}

	function connectToSalesforce(chatDescriptor) {
		chatDescriptor.chatId = chatDescriptor.userID ? chatDescriptor.userID : chatDescriptor.viewID;

		// salesforce contact search
		if (chatDescriptor.userID) {
			FSBL.Clients.DataTransferClient.openSharedData({
				data: {
					"salesforce.contact.search": {
						name: chatDescriptor.header
					}
				},
				publishOnly: true
			})
		}
	}

	function createdSalesforceTask(err, task) {
		if (err || !task) {
			console.error(err);
			FSBL.alertify.closeLogOnClick(true).error(err);
		} else {
			FSBL.alertify.log('Salesforce Task Created');
		}
	}

	function createSalesforceTask(sharedData) {
		var title;
		Object.keys(sharedData).forEach(function (sKey) {
			if (title) title += ' and '
			else title = ''
			title += (sharedData[sKey].description ? sharedData[sKey].description : sKey)
		});

		//return; //TODO sidd - re-enable this later
		var salesforceDomain = FSBL.Clients.WindowClient.options.customData.component.salesforceDomain;
		if (salesforceDomain) {
			sf.createTask({
				subject: 'Shared ' + title + ' on Symphony',
				searchTerm: FSBL.Clients.WindowClient.options.customData.chatInfo.header,
				objectType: (FSBL.Clients.WindowClient.options.customData.chatInfo.userID ? 'Contact' : 'Account'),
				redirectUri: location.href,
				baseUrl: salesforceDomain
			}, createdSalesforceTask)
		}

	}

	switch (FSBL.Clients.WindowClient.options.customData.component.type) {
		case "Symphony": //Navigation Component

			// Custom styles to hide everything except navigation
			FSBL.$(document.body).append(`<style>
				#grid_modal, #simple_grid { display: none !important}
				#nav, .navigation-main, .navbar {width: 100% !important;}
				#nav {top:0px !important}
				.navigation-category__title-wrapper {display: none;}
			</style>`);

			// Check if navigation has changed and keep hiding elements that are not supposed to be visible if they pop up again
			function navHide(attempt) {
				if (checkIfSignonNeeded()) { return; }
				var hidden = false;
				var navItems = FSBL.$('.navigation-category-name');
				navItems.each(function (key, value) {
					var title = FSBL.$(value).text();
					if (title === 'Signals' || title === 'Applications') {
						FSBL.$(value).closest('.navigation-category-title').hide();
						FSBL.$(value).closest('.navigation-category').hide();
						hidden = true;
					}
				});

				if (!hidden) {
					setTimeout(function () {
						navHide(0);
					}, 500);
				} else if (attempt === 0) {
					amSignedOn();
					setTimeout(function () {
						navHide(1);
					}, 500);
				}
			}
			navHide(0);

			// prevent multiple clicks
			var lastClicked;
			function setLastClicked(text) {
				lastClicked = text;
				setTimeout(function () {
					lastClicked = '';
				}, 3000);
			}

			function navClickHandler(nameDiv) {
				var text = nameDiv.text();
				if (lastClicked === text) {
					return;
				} else {
					setLastClicked(text);
				}
				// Wait for the chat window to load, once loaded, spawn a new chat window with the chat Id and Header

				function waitForChat() {
					var chatLoaded = false;
					var chatTitles = FSBL.$('.truncate-text');
					if (!chatTitles.length) chatTitles = FSBL.$('.chat-room-header__name');
					chatTitles.each(function (key, value) {
						var chatHeader = FSBL.$(value);
						if (chatHeader.text() === text) {
							chatLoaded = true;
							var chatDescriptor = {
								header: text
							};

							chatDescriptor.userID = chatHeader.attr('data-userid');
							if (!chatDescriptor.userID) {
								chatDescriptor.viewID = chatHeader.closest('[data-viewid]').attr('data-viewid').split("chatroom")[1];
							}
							//FSBL.Clients.RouterClient.query('Finsemble.LaunchSalesforceWindow', chatDescriptor, function(err, response) {});
							launchIfNeeded(chatDescriptor);
							return false;
						}
					});
					if (!chatLoaded) {
						setTimeout(waitForChat, 250);
					}
				}
				waitForChat();
			}

			// Catch navigation clicks - this is needed to catch clicks on the entire div as opposed to just the name
			FSBL.$(document).on('click', '.navigation-item', function (event) {
				var nameDiv = $(this).find('.navigation-item-name').trigger('click');
				event.preventDefault();
				event.stopPropagation();
				navClickHandler(nameDiv);
				return false;
			});
			break;
		case "Symphony Chat": //Chat Component
			// Get state at load time
			FSBL.Clients.WindowClient.getComponentState({ field: 'chatInfo' }, function (err, state) {
				if (state) { FSBL.Clients.WindowClient.options.customData.chatInfo = state; }
				// set state to be used at reload time
				FSBL.Clients.WindowClient.setComponentState({ field: 'chatInfo', value: FSBL.Clients.WindowClient.options.customData.chatInfo });

				// Hide navigation and other elements, resize and reposition stuff
				FSBL.$('#nav').hide();
				FSBL.$(document.body).append('<style>html {height: auto !important; min-height: auto !important} body {max-height: calc(100% - 32px) !important; min-height: calc(100% - 32px) !important; height: calc(100% - 32px) !important; margin-top: 0px !important} body.enhanced-layout .message-group {max-width: inherit} body.enhanced-layout .module:not(.blast) .text-input-view {max-width: inherit;} body.enhanced-layout .module .module-actions {display: none;}</style>');
				FSBL.$(document.body).append('<style>#grid_modal {left: 0px !important; width: 100% !important} #simple_grid {left: 0px !important; width: 100% !important} .simple_grid_container {width: 100% !important}</style>');

				// Deal with symphony's strange chat resizing using scripts instead of styles. @Todo still fails in many cases.
				function chatResize() {
					var grid1 = FSBL.$('#grid_modal');
					var grid2 = FSBL.$('#simple_grid');
					if (grid1.length && grid2.length) {
						FSBL.$('#grid_modal').css('top', 32).css('left', 0).css('height', '100%').css('width', '100%');
						FSBL.$('#simple_grid').css('top', 32).css('left', 0).css('height', '100%').css('width', '100%');
						FSBL.$('.simple_grid_container').css('width', '100%');
						//FSBL.$('body.enhanced-layout .message-group').css('max-width', '')
					}// else {
					setTimeout(chatResize, 500);
					//}
				}
				chatResize();

				// Send Finsembleized message
				// Notes: cannot send any non-http/s links. Attempts to send other protocols like finsemble:// get converted to https://finsemble:// - which Chrome messes up internally as https://finsemble//. So can't beat em, join em.
				window.sendFinsembleMessage = function (message) {

					var inputBox = jQuery(".text-input-text");
					var p = inputBox.find("p");
					if (p.length < 1) {
						var p = jQuery("<p></p>");
						inputBox.append(p);
					}

					inputBox.focus();
					message = encodeURIComponent(JSON.stringify(message));

					var url = new URL("https://finsemble//" + message);
					var b = jQuery('<button>');
					b.addClass("entity colorable aliasable urls-entity");
					b.attr("contenteditable", "false");
					b.attr("data-type", "urls-entity");
					b.attr("data-value", url.href);
					b.attr("data-appears-in-body", "false");
					b.text("Test Text");
					b.append('<span contenteditable="false" class="cancel"></span>');

					p.append(b);
					p.append('&nbsp;');

					inputBox.focus();

					var e = jQuery.Event("keydown");
					e.which = 13;
					p.trigger(e);
					//jQuery('.rte-main__send-message-button').trigger('click');
				};

				function processSharedData(sharedData) {
					FSBL.Clients.DataTransferClient.openSharedData({ data: sharedData });
				}

				// Hijack the event dispatcher to catch finsemble// links
				function hijackEventDispatcher() {
					if (FSBL.evtHijacked) return;
					FSBL.evtHijacked = true;
					jQuery.event.oldDispatch = jQuery.event.dispatch;
					jQuery.event.dispatch = function (event) {
						if (event && event.currentTarget && event.currentTarget.href && ['click', 'mousedown', 'dragstart'].includes(event.type) && event.currentTarget.href.indexOf('finsemble//') !== -1) { //
							event.stopPropagation();
							event.preventDefault();
							var data = event.currentTarget.href.split('finsemble//')[1];
							if (event.type == 'dragstart') {
								FSBL.Clients.DataTransferClient.dragStartWithData(event.originalEvent, data);
							} else {
								processSharedData(JSON.parse(decodeURIComponent(data)));
							}
							return;
						} else {
							return jQuery.event.oldDispatch.call(this, event);
						}
					};
				}

				// Set up the drop zone.
				function setupDropZone() {
					console.log("I'm setting up the drop zone");
					if (FSBL.$('.simple_grid_main_container').length && !FSBL.$('.simple_grid_main_container').hasClass('drophere')) {
						FSBL.$('.simple_grid_main_container').addClass('drophere');
						FSBL.$(document.body)

							// Update the drop zone class on drag enter/leave - these classes dont do anything yet
							.on('dragenter', '.simple_grid_main_container', function (ev) {
								$(ev.target).addClass('dragover');
								return false;
							})
							.on('dragleave', '.simple_grid_main_container', function (ev) {
								$(ev.target).removeClass('dragover');
								return false;
							})

							// Allow drops of any kind into the zone.
							.on('dragover', '.fsbl-share-scrim', function (ev) {
								return false;
							})

							// Handle the final drop...
							.on('drop', '.fsbl-share-scrim', function (ev) {
								FSBL.Clients.DataTransferClient.drop(ev);
							});



					} else {
						setTimeout(setupDropZone, 250);
					}
				}
				setupDropZone();

				function sendData(err, response) {
					if (response.shareMethod != FSBL.Clients.DataTransferClient.SHARE_METHOD.DROP) {
						return;
					}
					var data = response.data;
					if (data['symphony.chat']) {
						delete data['symphony.chat'];
					}
					if (!Object.keys(data).length) return;
					sendFinsembleMessage(data);

					createSalesforceTask(data);
				}

				function openChat(err, response) {
					FSBL.Clients.WindowClient.options.customData.chatInfo = response.data['symphony.chat'].chatDescriptor;
					FSBL.Clients.WindowClient.setComponentState({ field: 'chatInfo', value: FSBL.Clients.WindowClient.options.customData.chatInfo }, function () {
						if (!location.href.includes(FSBL.Clients.WindowClient.options.customData.component.podUrl)) {
							location.href = FSBL.Clients.WindowClient.options.customData.component.podUrl;
						} else {
							chatLink();
						}
					});

				}

				//FSBL.Clients.DataTransferClient.openLinkerDataByDefault = false;
				FSBL.Clients.DataTransferClient.addReceivers({
					receivers: [
						{
							type: /.*/,
							handler: sendData
						},
						{
							type: 'symphony.chat',
							handler: openChat
						}
					]
				});

				// Load the correct chat that is supposed to load and not whatever symphony wants to by triggering a click on the proper chat.
				function chatLink() {
					if (checkIfSignonNeeded()) { return; }
					var navLinks = FSBL.$('.navigation-item-name');
					if (navLinks.length) {
						let foundChat = false;

						navLinks.each(function (key, value) {
							if (foundChat) { return; }
							var userChat = $(value).find('span[data-userid]');
							if (userChat.length) { // user chat
								if (userChat.attr('data-userid') == FSBL.Clients.WindowClient.options.customData.chatInfo.userID) {
									amSignedOn();
									$(value).trigger('click');
									foundChat = true;
									hijackEventDispatcher();
								}
							} else if ($(value).text() === FSBL.Clients.WindowClient.options.customData.chatInfo.header) {
								amSignedOn();
								$(value).trigger('click');
								foundChat = true;
								hijackEventDispatcher();
							}
						});
						if (!foundChat) {
							setTimeout(chatLink, 250);
						}

					} else {
						setTimeout(chatLink, 250);
					}

				}
				chatLink();
			});
			break;
	}

});


